.popup-quiz {
    .popup-book__wrapper {
        height: 536px !important;

        @media (max-width: 768px) {
            height: 100% !important;
            width: 100%;
            border-radius: 0 !important
        }
    }
}

.quiz-cross {
    width: 27px;
    height: 28px;
    color: #B5BBC2;

    @media (max-width: 768px) {
        width: 20px;
        height: 20px;
    }
}

.quiz-progress {
    @media (max-width: 768px) {
        font-size: 0.75rem;
    }
}

.progress-quiz {
    height: 0.5rem;

    & .progress-bar-striped {
        background-image: linear-gradient(122deg, rgba(255, 255, 255, 0.15) 25%, transparent 25%, transparent 50%, rgba(255, 255, 255, 0.15) 50%, rgba(255, 255, 255, 0.15) 75%, transparent 75%, transparent) !important;
        background-color: #1F3246;
        background-position-x: -4%;
        transition: all .3s ease;
    }
}

.quiz-back {
    .default-icon {
        transform: rotate(180deg)
    }
}

.content-quiz {
    &__title {
        font-size: 1.5rem;

        @media (max-width: 768px) {
            font-size: 1.125rem;
        }
    }

    &__checkbox {
        background: var(--light-background);
        transition: all .3s ease;
        cursor: pointer;

        &.active {
            border: 1px solid #fbbf24 !important;
        }

        @media (max-width: 768px) {
            font-size: 0.875rem;
        }
    }

    &__map {
        height: 160px;
    }
}

img.marker {
    width: 30px;
    position: absolute;
    left: -15px;
    top: -15px;
}

.quiz-warning {
    a {
        text-decoration: underline;
    }

    @media (max-width: 768px) {
        text-align: center;
    }
}

.quiz-header {
    @media (max-width: 768px) {
        border-bottom: 1px solid var(--bs-semi-gray)
    }
}